import React from 'react'
import { compose } from 'redux'
import { navigate } from 'gatsby'

import Page from 'containers/MemoPage/Steps/Step3'
import creatingHoc from 'containers/MemoPage/creatingHoc'
import storingHoc from 'containers/MemoPage/storingHoc'

export default compose(creatingHoc, storingHoc)(props => (
  <Page
    onNext={() => navigate('/memo/notice')}
    onEdit={() => navigate('/memo/new/1')}
    {...props}
  />
))
